body {
  background-color: #D6E2FA;
  background-image: url("/img/bg.jpg");
  text-align: center;
  padding: 20px 32px;
  font-family: "Montserrat", sans-serif;
  margin: 0;
}

.container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: flex-end;
  max-width: 1500px;
  margin: 0 auto;
}

.container.rules {
  display: block;
  max-width: 1000px;
  text-align: left;
  background-color: #fff;
  padding: 48px;
  margin-top: 50px;
  line-height: 1.4;
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
}
.header .header__menu {
  display: flex;
}
.header .header__link {
  display: flex;
  align-items: center;
  font-size: 18px;
  font-weight: 900;
  text-decoration: none;
  color: #8886fc;
  margin-left: 24px;
}
.header .header__link:hover {
  color: #5e5ccf;
}
@media (max-width: 800px) {
  .header .header__link {
    font-size: 0px;
  }
}
@media (max-width: 800px) {
  .header {
    padding: 0 12px;
  }
}

.step {
  cursor: pointer;
  padding: 20px;
  box-sizing: border-box;
}
.step.disabled {
  filter: grayscale(100%);
  cursor: default;
}
.step .svg-wrapper {
  padding-bottom: 110%;
  position: relative;
  overflow: visible;
  transition: transform 300ms cubic-bezier(0.16, 1, 0.3, 1);
}
.step .svg-wrapper svg {
  position: absolute;
  right: 0;
  bottom: 0;
  left: 0;
}
@media (min-width: 1200px) {
  .step {
    width: 25%;
  }
  .step:nth-child(2n) {
    position: relative;
    top: 160px;
  }
}
@media (min-width: 800px) and (max-width: 1200px) {
  .step {
    width: 33%;
  }
  .step:nth-child(3n-1) {
    position: relative;
    top: 120px;
  }
}
@media (max-width: 800px) {
  .step {
    width: 50%;
    margin-bottom: 50px;
  }
}
@media (max-width: 600px) {
  .step {
    width: 70%;
    margin-bottom: 30px;
  }
}
.step:hover .svg-wrapper {
  transform: scale(1) translateY(-20px);
}

.title {
  color: #06c39f;
  font-size: 60px;
  margin-top: 40px;
  margin-bottom: 10px;
  text-transform: uppercase;
}
@media (max-width: 600px) {
  .title {
    font-size: 32px;
  }
}

.subtitle {
  max-width: 600px;
  margin: 0 auto;
  margin-bottom: 80px;
  font-size: 15px;
  line-height: 1.4;
  opacity: 0.8;
}

/* The Modal (background) */
.modal {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1060;
  display: none;
  width: 100vw;
  height: 100vh;
  overflow-x: hidden;
  overflow-y: auto;
  outline: 0;
  background-image: url("/img/pattern.png");
  background-color: rgba(0, 0, 0, 0.7);
  animation-name: shade;
  animation-duration: 0.2s;
}

/* Modal Content */
.modal-content {
  background-color: #ffffff;
  margin: 100px auto;
  padding: 0;
  max-width: 600px;
  animation-name: animatetop;
  animation-duration: 0.4s;
}

/* Add Animation */
@keyframes animatetop {
  from {
    transform: translateY(100px);
    opacity: 0;
  }
  to {
    transform: translateY(0);
    opacity: 1;
  }
}
@keyframes shade {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
/* The Close Button */
.close {
  position: absolute;
  top: 10px;
  right: 10px;
  cursor: pointer;
}

.close:hover,
.close:focus {
  opacity: 0.7;
}

.modal-header {
  padding: 12px;
  background-color: #fbae2f;
  color: white;
  margin-bottom: 40px;
  position: relative;
}

.modal-body {
  padding: 2px 16px;
}

#start {
  animation: shake 2s cubic-bezier(0.36, 0.07, 0.19, 0.97) infinite;
  animation-delay: 500ms;
}
#start:hover, #start.stopped {
  animation: none;
}

#start #hand {
  transform: translateY(600px) translateX(150px) scale(0.3);
  transition: transform 600ms cubic-bezier(0.16, 1, 0.3, 1);
  transition-delay: 300ms;
}

#start:hover #hand {
  transform: translateY(0px) translateX(0px) scale(1);
  transition-delay: 100ms;
}

#start:hover #btn {
  transform: translateX(15px) translateY(-15px);
  transition: transform 600ms cubic-bezier(0.16, 1, 0.3, 1);
}

#start #flag {
  transform: translateY(280px) translateX(290px) scale(0);
  transition: all 200ms cubic-bezier(0.16, 1, 0.3, 1);
  transition-delay: 200ms;
}

#start:hover #flag {
  transform: translateY(0px) translateX(0px) scale(1);
  transition-delay: 790ms;
}

#start:hover #finger {
  animation: hand-anim-finger 400ms ease-in-out;
  animation-delay: 700ms;
  animation-iteration-count: 1;
  animation-direction: alternate-reverse;
}

@keyframes hand-anim-finger {
  from {
    transform: translateY(0) translateX(0);
  }
  to {
    transform: translateY(15px) translateX(-14px);
  }
}
@keyframes shake {
  5% {
    transform: translateX(-1px);
  }
  10%, 40% {
    transform: translateX(2px);
  }
  15%, 45% {
    transform: translateX(-2px);
  }
  20%, 30% {
    transform: translateX(4px);
  }
  25%, 35% {
    transform: translateX(-4px);
  }
  50% {
    transform: translateX(0);
  }
}